.page-list {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.list-page-loading {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sider-image {
  width: 25px;
  height: 25px;
  margin: 0px 15px 3px 0px;
}